.login-session {
  max-height: 100%;
}

.login-session.hidden {
  visibility: hidden;
  opacity: 0;
  max-height: 0;
  transition: visibility 0s 1s, opacity 1s linear, max-height 1s linear;
}

.login-session p {
  margin: 0;
}

.login-session p:last-of-type {
  padding-bottom: 20px;
}

.login-session__logout {
  font-weight: bold;
  padding: 0;
}

.session-manager-field {
  padding: 0.5385rem 0;
}




